.pretty .state label:before {
    border-color: #bdc3c7;
}

.pretty .state label:after, .checkbox-panel .state img, .pretty .state label:before{
    width: 30px !important;
    height: 30px !important;
    margin-top: 15px;
}

.pretty .state label{    padding-left: 1.75em;
    color: #737B80;
    font-size: 14px;
    cursor: pointer;
    line-height: 15px;
}

.pretty.p-svg.p-plain {
    width: 30%;
}

.pretty .state label {
    white-space: pre-wrap;
}

.pretty.p-svg .state .svg {
    margin-top: 15px;
}

.pretty.p-svg input:checked~.state .svg {
  margin-top: 15px;
}

.pretty:first-child .state label:after, .checkbox-panel .pretty:first-child img, .pretty:first-child .state label:before {
    margin-top: 0px !important;
}

input[type=checkbox] + .accord-text:before {
 content: '';
    width: calc(1em + 2px);
    height: calc(1em + 2px);
    display: block;
    box-sizing: border-box;
    border-radius: 0;
    border: 1px solid transparent;
    z-index: 0;
    position: absolute;
    left: 0;
    top: calc((0% - (100% - 1em)) - 8%);
    background-color: transparent;
}
input[type=checkbox]:checked + .accord-text:before {
    content: '';
    width: calc(1em + 2px);
    height: calc(1em + 2px);
    display: block;
    box-sizing: border-box;
    border-radius: 0;
    border: 1px solid transparent;
    z-index: 0;
    position: absolute;
    left: 0;
    top: calc((0% - (100% - 1em)) - 8%);
    background-color: transparent;
}


